<template>
  <div class="design">
    <div class="numberSum">
       <div class="taglabe"></div>
       <div class="labelStyle">
         <div>20177</div>
         <div>1<span style="color: #198bfe;font-weight: bolder;">3108</span></div>
         <div>2</div>
       </div>
       <div id="hunmanDetail"></div>
    </div>
    <div class="numberSum">
      <div class="taglabe"></div>
      <div class="labelStyle">
        <div><span style="color: #198bfe;font-weight: bolder;">3108</span>,:</div>
      </div>
      <div id="hrfb"></div>
    </div>
  </div>
</template>

<script>
import echarts from "echarts"
export default {
  name: 'hunmaNum',
  data(){
    return{
      activeName:"first",

    }
  },
  methods:{
    handleClick(){
      console.log("click");
    }
  },
  activated (){
    let hunmanDetail = echarts.init(document.getElementById('hunmanDetail'));
    let hroption = {
      tooltip : {
        trigger: 'axis',
        axisPointer: {
          type: 'none'
        },
        formatter: function (params) {
          // console.log(b[i]);
          for (var i = 0, l = hroption.xAxis[0].data.length; i < l; i++) {
            // console.log(b[i]);
            if (hroption.xAxis[0].data[i] == params[0].name){
              return params[0].name + '<br/>'+
                "" + params[0].value;
            }
          }
        }
      },
      grid: {
        x: 60,
        y: 25,
        x2: 20,
        y2: 50,
        backgroundColor: 'rgba(0,0,0,0)',
        borderWidth: 0,
        borderColor: 'white'
      },
      xAxis : [
        {
          type : 'category',
          boundaryGap : false,
          data : ["14","15","15","15","15","16","16","16","16","17","17",""],
          axisLine:{
            show:false,
          },
          axisTick :{
            show:false,
          },
          splitLine : {
            show:false,
          },
          axisLabel:{
            interval: 0,//
            formatter:function(params){
              var newParamsName = "";// 
              if(params)
              {
                var paramsNameNumber = params.length;// 
                var provideNumber = 4;// 
                var rowNumber = Math.ceil(paramsNameNumber / provideNumber);// 
                /**
                 *   
                 */
                // rowNumber>1
                if (paramsNameNumber > provideNumber) {
                  /** ,p */
                  for (var p = 0; p < rowNumber; p++) {
                    var tempStr = "";// 
                    var start = p * provideNumber;// 
                    var end = start + provideNumber;// 
                    // 
                    if (p == rowNumber - 1) {
                      // 
                      tempStr = params.substring(start, paramsNameNumber);
                    } else {
                      // 
                      tempStr = params.substring(start, end) + "\n";
                    }
                    newParamsName += tempStr;// 
                  }

                } else {
                  // 
                  newParamsName = params;
                }
                //
              }

              return newParamsName;
            },
            textStyle:{color:"#fff"}
          }
        }
      ],
      yAxis : [
        {
          type : 'value',
          axisLabel : {
            formatter: '{value}',
            margin : 16,
            textStyle:{color:"#fff"}
          },
          axisLine:{
            show:false,
          },
          axisTick :{
            show:false,
          },
          scale : true,
          splitNumber:4,
          splitLine: {
            lineStyle: {
              // 
              color: ['#ddd']
            }
          }
        }
      ],
      label:{
        normal:{
          show: true,
          position: 'top',
          textStyle :{
            color :'#008BDE',
          }
        },
        emphasis:{
          show: true,
          position: 'top',
          textStyle :{
            color :'#008BDE',
          }
        },
      },
      series : [
        {
          name:'',
          type:'line',
          data:[1444,1492,1485,1531,1612,1753,1853,1956,2145,2784,3022,3200],
//          symbol:'image://assets/circleblue.png',
          symbolSize: function (value,pra){
            return Math.round((pra.name.indexOf('')!=-1 || pra.name.indexOf('')!=-1?13:4)*2);
          },
          smooth :false,
          itemStyle : {
            normal : {
              color : "#2794D7",
            }
          }
        }
      ]
    };
    hunmanDetail.setOption(hroption);
    let hrfbEchart = echarts.init(document.getElementById('hrfb'));
    let fboption = {
      tooltip : {
        trigger: 'axis',
        axisPointer: {
          type: 'none'
        }
      },
      grid: {
        x: 20,
        y: 15,
        x2: 20,
        y2: 50,
        backgroundColor: 'rgba(0,0,0,0)',
        borderWidth: 0,
        borderColor: 'white'
      },
      label:{
        normal:{
          show: true,
          position: 'top',
          textStyle :{
            color :'#008BDE',
          }
        },
        emphasis:{
          show: true,
          position: 'top',
          textStyle :{
            color :'#F94B64',
          }
        },
      },
      calculable : true,
      xAxis : [
        {
          type : 'category',
          data : ["","","","","",""],
          show:true,
          axisLine:{
            show:false,
          },
          axisTick:{
            show:false,
          },
          splitLine:{
            show:false,
          },
          axisLabel:{
            interval: 0,//
            formatter:function(params){
              var newParamsName = "";// 
              if(params)
              {
                var paramsNameNumber = params.length;// 
                var provideNumber = 3;// 
                var rowNumber = Math.ceil(paramsNameNumber / provideNumber);// 
                /**
                 *   
                 */
                // rowNumber>1
                if (paramsNameNumber > provideNumber) {
                  /** ,p */
                  for (var p = 0; p < rowNumber; p++) {
                    var tempStr = "";// 
                    var start = p * provideNumber;// 
                    var end = start + provideNumber;// 
                    // 
                    if (p == rowNumber - 1) {
                      // 
                      tempStr = params.substring(start, paramsNameNumber);
                    } else {
                      // 
                      tempStr = params.substring(start, end) + "\n";
                    }
                    newParamsName += tempStr;// 
                  }

                } else {
                  // 
                  newParamsName = params;
                }
              }
              //
              return newParamsName;
            }
          }
        }
      ],
      yAxis : [
        {
          type : 'value',
          show:false,
          splitLine:{
            show:false,
          },
        }
      ],
      series : [
        {
          name:'',
          type:'bar',
          data:[2210,400,300,400,645,290],
          barWidth : 15,//
          itemStyle:{
            normal:{
              color:'#008BDE',
              barBorderRadius:[8, 8, 8, 8],
            },
            emphasis:{
              color:'#F94B64',
              barBorderRadius:[8, 8, 8, 8],
            },
          },
        },

      ]
    };
    hrfbEchart.setOption(fboption);
  }
}
</script>

<style  lang="scss">
  .design{
    background: #01304a;
    border-top: #1d87ff solid 1px;
    width: 100%;
    height: 100%;
    margin-top: 45px;
    color:white;
    margin-top:45px;
    .top_title{
      height: 50px;
      line-height: 50px;
      text-align: center;
      color: #fff;
    }
  }
 .title{
   font-size: 20px;
   margin-left: 90px;
 }
 .itembg{
   background-color:#023c64;
   border:#184e7d 2px solid;
 }
 .numberSum{
   width: 100%;
   margin-top:10px;
 }

 .taglabe{
   padding-left: 10px;
   font-size: 16px;
   height: 30px;
   line-height: 30px;
   width: 40%;
   border-radius: 0 30px 0 0;
   background-color: #198bfe;
 }
 #hrfb,#hunmanDetail{
   width: 100%;
   height: 300px;
 }
 .labelStyle{
   font-size: 12px;
   margin-left: 10px;
   margin-top: 5px;
   margin-bottom: 10px;
 }
  .el-menu--horizontal.el-menu--dark .el-menu-item:hover, .el-menu--horizontal.el-menu--dark .el-submenu__title:hover{
    background: #023c64;
  }
  .el-menu{
    -webkit-border-radius:0;
    -moz-border-radius:0;
    border-radius:0;
  }
  /*.el-menu--dark .el-menu-item, .el-menu--dark .el-submenu__title{*/
    /*color: white;*/
  /*}*/
  .el-table tr,.el-table th>.cell,.el-table__footer-wrapper tbody td{
    background: #023c64;
    color: #fff;
  }
  .el-table th>.cell,.el-table th{
    background-color:#198bfe;
  }
</style>



