import Vue from 'vue'
import Router from 'vue-router'
import humanum from '../page/humanum'
import login from '../page/login'  // 
import deviation from '../page/deviation' // -
import costunits from '../page/costunits' // 
import cost from '../page/cost'  // 
import Cdcapital from '../page/Cdcapital'  // 
import crashReports from '../page/crashReports'  // 
import situation from '../page/situation'  // 
import annualindicator from '../page/annualindicator' // 
import  sales from "../page/sales" // 
import backmoney from '../page/backmoney' // 
import keyIndicators from '../page/keyIndicators'  // 
import  receivable from "../page/payment.vue"// 
import  project from "../page/project"// 
import mainContainer from '../page/mainContainer'
import percapeffica from '../page/PerCapitaEfficacy'
import adviceSubmit from '../page/adviceSubmit'
import designDetail from '../page/designDetail'
import header from '../components/headerDiv.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login,
      children:[
        {
          path:"/",
          component:login
        },
      ]
    },
    {
      path: '/',
      name: 'mainContainer',
      component: mainContainer,
      redirect: '/annualindicator',
      children:[
        {
          path:"/humanum",
          component:humanum
        },
        {
          path:"/cost",
          component:cost
        },
        {
          path:"/deviation",
          component:deviation
        },
        {
          path:"/costunits",
          component:costunits
        },
        {
          path:"/Cdcapital",
          component:Cdcapital
        },
        {
          path:"/crashReports",
          component:crashReports
        },
        {
          path:"/situation",
          component:situation
        },
        {
          path:"/annualindicator",
          component:annualindicator
        },
        {
          path:"/backmoney",
          component:backmoney
        },
        {
          path:"/perce",
          component:percapeffica
        },
        {
          path:"/advice",
          component:adviceSubmit
        },
        {
          path:"/keyIndicators",
          component:keyIndicators
        },
        {
          path:"/receivable",
          component:receivable
        },
        {
          path:'/project',
          component:project
        },
        {
          path:"sales",
          component:sales
        }
      ]
    },
  ]
})



